
import Checklist from 'vux/src/components/checklist/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'

export default {
components: {
Checklist,
CheckIcon,
CartList: resolve => require(["./chatList/index.vue"], resolve),
hot: resolve => require(["@/views/Assemble/hotness/index.vue"], resolve)
},
data() {
return {
key: "value",
demo1: false,
freeState: {},
bottomMsge: {},
arr: [],
goodList: []


};
},
watch: {
bottomMsge: {
handler(val) {
this.bottomMsge = val;
}
}
},
methods: {

payPage() {
var arr = [];
var count = 0;
this.goodList.forEach(e => {
if (e.ischeck) {
count += e.count;
arr.push({
id: e.goodsId,
num: e.count

});
}
});
if (!arr.length) {
return;
}
var obj = {
openId: localStorage.getItem("openId"),
goodList: arr
};
obj = JSON.stringify(obj);
this.$router.push("/paysure?data=" + obj + "&count="+count);
},

changeNum(e, arr) {
console.log(e);
this.$parent.buyNum = e;
localStorage.setItem('catnum',e);
},

getBottom(val) {
this.bottomMsge = val;
},

pack(val) {
this.freeState = val;
},

getArray(arr, n) {
if (!arr.length) {
this.goodList = arr;
return;
}
this.goodList = arr;
},

goDetail(item) {
console.log(item)
this.$router.push("/goodsdetail?id=" + item.id);
}
},

created() {
settitle("购物车");
},
mounted() {

}
};



